.card
{
    width: 250px;
    height: 300px;
    display: flex;
    flex-direction: column ;
    justify-content: space-around;
}

label {
    width: max-content;
    color: tomato ;
    font-size: 13px;
    border-bottom : 1px solid tomato ;
    margin : 3px 0
}

.star {
    color: gold;
}

form {
    width: 250px;
    /* height: 250px; */
    display: flex;
    flex-direction: column;
    justify-content: center;
}
input {
    border: none;
    border-bottom: 1px solid gray;
  }
  
  input::placeholder,
  textarea::placeholder {
    font-size: 12px;
    color: rgb(185, 184, 184);
  }
  
  input:focus,
  textarea:focus,
  select:focus {
    outline: none;
  }
  
  textarea {
    border: none;
    border-bottom: 1px solid gray;
  }
  
  .submitButton {
    border: none;
    padding: 5px;
    border-radius: 5px;
    color: white;
    background-color: tomato;
    cursor: pointer;
  }
  .buttons,
  .logout {
    position: absolute;
    top: 10px;
    right: 10px;
  }
  
  .button {
    border: none;
    padding: 5px;
    border-radius: 5px;
    color: white;
    font-weight: 500;
    cursor: pointer;
  }
  
  .button:focus{
    outline: none;
  }
  
  