input {
    border: none;
    border-bottom: 1px solid gray;
  }
  
  input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    font-size: 12px;
    color: rgb(185, 184, 184);
  }
  
  input:-ms-input-placeholder, textarea:-ms-input-placeholder {
    font-size: 12px;
    color: rgb(185, 184, 184);
  }
  
  input::placeholder,
  textarea::placeholder {
    font-size: 12px;
    color: rgb(185, 184, 184);
  }
  
  input:focus,
  textarea:focus,
  select:focus {
    outline: none;
  }
  
  textarea {
    border: none;
    border-bottom: 1px solid gray;
  }
  
  .submitButton {
